    <span class="hljs-tag">&lt;<span class="hljs-name">t-progress</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"32"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-progress</span>&gt;</span>
