<p>使用 type 属性来定义 Progress 的样式</p>
<div class="demo-block">
    <t-progress type="primary" v-model="progress"></t-progress>
    <t-button @click="progress++">+</t-button>
    <t-button @click="progress--">-</t-button>
</div>
