        <span class="hljs-tag">&lt;<span class="hljs-name">t-tab</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"second"</span>&gt;</span>Second<span class="hljs-tag">&lt;/<span class="hljs-name">t-tab</span>&gt;</span>
